import React from 'react';

const BackSides = ({ src, numTimes }) => {
  const items = [];
  
  for (let i = 0; i < numTimes; i++) {
    let imgData = `back-side-${i}`;
    
    items.push(
      <img key={i} className={imgData} src={src} alt={imgData} />
    );
  }

  return (
    <div className='cards-back-sides stack'>
      {items}
    </div>
  );
};

export default BackSides;
